










import Vue from "vue";
import Component from "vue-class-component";
import { UniteLabels } from "@/logic/api";
import { enumStringToOptions } from "@/logic/types";

const UniteFieldProps = Vue.extend({
  props: {
    unite: String,
    label: {
      type: String,
      default: "Unité",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    unites: {
      type: Array,
      default: function () {
        return enumStringToOptions(UniteLabels);
      },
    },
  },
  model: {
    prop: "unite",
    event: "change",
  },
});

@Component({})
export default class UniteField extends UniteFieldProps {}
